
import { defineComponent } from '@vue/runtime-core'
import { onMounted, ref } from 'vue'
export default defineComponent({
  setup() {
    const lock = ref(false)
    onMounted(() => {
      lock.value = true
    })

    function clossText() {
      lock.value = false
    }
    return {
      lock,
      clossText
    }
  }
})
